.App {
  /* text-align: center; */
}

h1 {
  text-align: center;
}

form {
  display: flex;
  justify-content: center;
}

.App input {
  width: 170px;
  border-color: blue;
  padding: 10px 5px;
}

.sign-up {
  margin-left: 20px;
  background-color: indianred;
  padding: 8px 19px;
  color: antiquewhite;
  border: none;
  
}

.first-list {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  padding: 0;
}

.first-list li {
margin: auto;
list-style: none;
padding: 0 2%;
}

h2 {
  text-align: center;
}

.features {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.features p {
  background-color: rgb(195, 206, 206);
  width: 100px;
  height: 100px;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
}

@media screen and ( max-width: 328px ) {
  body {
    background-color: blue;
  }

  form {
    flex-direction: column;
    align-items: center;
    margin-left: 0;
  }

  .features {
    flex-direction: column;
    align-items: center;
  }

  .features p {
    width: 50%;
  }
}